.courcesPage__tabArea{margin:30px 0 0;overflow:hidden}.courcesPage__tabArea__wrap{box-sizing:border-box}.courcesPage__tabArea__wrap__tabNav{background:#f5f5f5;display:flex;flex-wrap:wrap;padding-left:0;margin-bottom:0;list-style:none}.courcesPage__tabContant{padding:35px 0 15px 0;margin-top:0}.courcesPage__tabContant__item1{background-color:#fff}.courcesPage__tabContant__item1__courceFeatures{list-style-type:none;grid-template-columns:auto auto;display:grid;position:relative}@media only screen and (max-width: 319px){.courcesPage__tabContant__item1__courceFeatures{display:block}}@media only screen and (min-width: 320px)and (max-width: 480px){.courcesPage__tabContant__item1__courceFeatures{display:block}}@media only screen and (min-width: 480px)and (max-width: 767px){.courcesPage__tabContant__item1__courceFeatures{display:block}}@media only screen and (min-width: 767px)and (max-width: 1025px){.courcesPage__tabContant__item1__courceFeatures{display:block}}.courcesPage__tabContant__item1__courceFeatures>li{padding-left:30px;position:relative;margin-bottom:10px;margin-right:5px;color:#444}.courcesPage__tabContant__item1__courceFeatures>li::before{content:"";position:absolute;font-family:FontAwesome;left:0;top:0;font-size:20px;border-radius:50%;z-index:1;line-height:1.3;color:#fdc800}.list{display:inline-block;margin:-1px;border-right:1px solid #cdcdcd;transition:all .5s ease-out;color:#002147;padding:22px 30px;text-transform:capitalize;font-weight:500;transition:all .5s ease-out;cursor:pointer}.active{background:#002147;color:#fff}