@import "../../../public/Style/_breakpoints";

.mainBanner {
  width: 100%;
  height: 100%;

  &__title {
    color: #ffffff;
    text-transform: capitalize;
    font-size: 56px;
    margin-bottom: 10px;
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    width: 100%;
    text-align: center;
    padding: 8px 0;
    @include if-device(tablet) {
      font-size: 40px;
    }
    @include if-device(largephone) {
      font-size: 30px;
    }
    @include if-device(phone) {
      font-size: 20px;
    }
  }

  &__desc {
    font-size: 16px;
    margin-bottom: 50px;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    color: #fff;
    padding: 5px;
    width: 750px;
    text-align: center;
    line-height: 1.5;
    @include if-device(tablet) {
      width: 100%;
    }
    @include if-device(largephone) {
      display: none;
    }
    @include if-device(phone) {
      display: none;
    }
  }

  &__startBtn {
    color: #ffffff;
    padding: 17px 0;
    background: #002147;
    text-transform: uppercase;
    font-size: 14px;
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    border: none;
    width: 162px;
    text-align: center;
    transition: all 0.5s ease-out;
    cursor: pointer;

    &:hover {
      background: #fdc800;
      color: #000;
    }
    @include if-device(largephone) {
      width: 150px;
      padding: 14px 0;
    }
    @include if-device(phone) {
      display: none;
    }
  }

  &__bannerImages {
    width: 100%;
    height: 529px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
    }

    @include if-device(tablet) {
      width: 100%;
      height: 100%;
    }
    @include if-device(largephone) {
      width: 100%;
      height: 100%;
    }
    @include if-device(phone) {
      width: 100%;
      height: 100%;
    }

    &__content {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      background-color: rgba(0, 0, 0, 0.5);

      @include if-device(desktop) {
        width: 100%;
        padding: 0 50px;
      }
      @include if-device(tablet) {
        width: 100%;
        padding: 0 50px;
      }
      @include if-device(largephone) {
        width: 100%;
        padding: 0 50px;
      }
      @include if-device(phone) {
        width: 100%;
        padding: 0 20px;
      }
    }
  }
}
