@import "../../../public/Style/breakpoints";

.banner {
  width: 100%;
  position: relative;
  height: 315px;
  background-image: url("../../Assets/ban.webp");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.75);
  }

  &__container {
    width: 1164px;
    margin: 0 auto;
    padding: 0 15px;

    &__body {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);

      &__title {
        &__h1 {
          text-transform: capitalize;
          font-family: "Roboto", sans-serif;
          font-weight: 700;
          margin-bottom: 5px;
          font-size: 48px;
          color: #ffffff;

          @include if-device(phone) {
            font-size: 32px;
          }

          @include if-device(tablet) {
            font-size: 42px;
          }

          @include if-device(largephone) {
            font-size: 42px;
          }

          @include if-device(desktop) {
            font-size: 42px;
          }
        }
      }

      &__path {
        display: flex;
        align-items: center;
        column-gap: 5px;

        &__start,
        &__mid,
        &__end {
          color: #fdc800;
          font-family: "Roboto", sans-serif;
          font-weight: 400;
          font-size: 14px;
          text-transform: capitalize;
        }

        &__mid,
        &__end {
          color: #fff;
        }
      }
    }
  }

  &__path {
    display: flex;
    align-items: center;
    column-gap: 5px;

    &__start,
    &__mid,
    &__end {
      color: #fdc800;
      font-family: "Roboto", sans-serif;
      font-weight: 400;
      font-size: 14px;
    }

    &__mid,
    &__end {
      color: #fff;
    }
  }
}
