@import "../../../public/Style/breakpoints";

.reviewArea {
  &__media {
    display: flex;
    padding-bottom: 8px;
    margin-bottom: 35px;

    @include if-device(smallPhone) {
      display: block;
    }

    @include if-device(phone) {
      display: block;
    }

    @include if-device(largePhone) {
      display: block;
    }

    &__image {
      max-width: 67px;

      & > img {
        width: 100%;
      }
    }

    &__bodyArea {
      flex: 1;
      position: relative;
      padding-left: 20px;

      @include if-device(phone) {
        padding: 0px;
        margin-top: 20px;
      }

      @include if-device(smallPhone) {
        padding: 0px;
        margin-top: 20px;
      }

      & > h3 {
        font-size: 16px;
        margin-bottom: 8px;
        font-weight: 500;
        margin: 0 0 20px 0;
        color: #212121;
      }

      & > h4 {
        color: #ffffff;
        background: #002147;
        padding: 2px 10px;
        display: inline-block;
        margin-bottom: 20px;
        font-size: 14px;
        font-weight: 500;
      }

      & > p {
        color: #757575;
        text-align: justify;
        line-height: 1.5;
        margin: 0 0 20px 0;
      }

      &__replyArea {
        position: absolute;
        right: 0;
        top: 35px;
        z-index: 3;

        & > ul {
          list-style: outside none none;
          margin: 0;
          padding: 0;
          display: inline-block;

          & > li {
            display: inline-block;

            & > i {
              color: #fdc800;
              display: inline-block;
              font: normal normal normal 14px/1 FontAwesome;
              font-size: inherit;
              text-rendering: auto;
            }
          }
        }
      }
    }
  }
}
