.courcesPage{box-sizing:border-box}.courcesPage__Box{margin-bottom:30px;background:#fff}.courcesPage__Box__inner{padding:30px 25px}@media only screen and (min-width: 767px)and (max-width: 1025px){.courcesPage__Box__inner{padding:30px 15px}}.courcesPage__Box__inner__courcePrice{box-sizing:border-box}.courcesPage__Box__inner__courcePrice>span{font-size:40px;color:#002147;margin-bottom:20px;display:block}.courcesPage__Box__inner__courcePrice__enrollBtn{margin-bottom:30px;color:#002147;padding:18px 0;background:#fdc800;text-transform:uppercase;font-size:14px;font-weight:700;display:inline-block;width:100%;border:2px solid #fdc800;text-align:center;transition:all .5s ease-out;cursor:pointer}.courcesPage__Box__inner__courcePrice__enrollBtn:hover{background-color:#fff;color:#fdc800}.courcesPage__Box__inner__courcePrice__pdf{position:relative;color:#002147;padding:18px 0;background:rgba(0,0,0,0);text-transform:uppercase;font-size:14px;font-weight:700;width:100%;border:1px solid #002147;text-align:center;transition:all .5s ease-out;border:2px solid #002147}.courcesPage__Box__inner__courcePrice__pdf::before{content:"";position:absolute;font-family:FontAwesome;color:#002147;top:50%;left:8%;z-index:1;transform:translateY(-50%);transition:all .5s ease-out}.courcesPage__Box__inner__courcePrice__pdf:hover{background-color:#fdc800;color:#fff;border:2px solid #fdc800}.courcesPage__Box__inner__courcePrice__pdf:hover::before{color:#fff}